<template>
  <div class="wrapper">
    <div class="stars">
      <div v-for='(star,index) in 5'>
        <span class="star-off star"></span>
        <span class="star-on star" :style='{width:(index < score ?"100":"0")+ "%"}' v-if='index < parseInt(score) '></span>
        <span class="star-on star" :style='{width:(index == Math.ceil(score) - 1 ?100*("0." + score.split(".")[1]):"0")+ "%"}' v-else></span>
      </div>
      <font v-if="ifShow">{{ score }}</font>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'vueRateShow',
    props: {
      score:{
        type:String,
        default:'0'
      },
      ifShow:{
        type: Boolean,
        default: true
      }
    },
    methods:{
      numRanty(index){
        this.num = index + 1
      }
    }
  }
</script>

<style scoped>
  .wrapper .stars{
    display: flex;
    align-items: center;
    margin-bottom: 8px;
  }
  .wrapper .stars font{
    color:#7d7d7d;
    font-size: 30px;
    margin-left: 50px;
  }
  .wrapper .stars>div{
    position: relative;
    width: 55px;
    height: 54px;
    display: inline-block;
    margin-right: 8px;
  }
  .wrapper .star{
    position: absolute;
    width: 55px;
    height: 54px;
  }
  .wrapper .star-on{
    width: 225px;
    background: url('./on.png') no-repeat;
    background-size: cover;
  }
  .wrapper .star-off{
    background: url('./off.png') no-repeat;
    background-size: cover;
  }
</style>



